import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	strict: false,
	state: {
		current: '',
		bloglist:['github','csharpstring']
	},
	mutations: {
		changeCurrent: function(state, param) {
			state.current = param
		}
	},
	actions: {
		changeCurrent: function(ctx, param) {
			ctx.commit('changeCurrent', param)
		}
	}
})
