<template>
	<div>
		<ul>
			<li v-for="key in privatebloglist" v-bind:key="key">
				<el-button type="text" @click="handleSelect(key)">{{key}}</el-button>
			</li>
		</ul>
		
	</div>
</template>
<script>
	export default {
		data(){
			return{
				privatebloglist:this.$store.state.bloglist
			}
		},
		methods: {
			handleSelect(key) {
				this.$store.commit('changeCurrent', key)	
			},
		}
	}
</script>
