<template>
	<div id="app">
		<el-container>
			<el-container style="padding-top: 50px;padding-bottom: 50px; background-color: #afd7ff;">
				<el-row :gutter="20" style="width: 100%;">
					<el-col :span="4" :offset="3">
						<headerpageleft style="background-color: #afd7ff;"></headerpageleft>
					</el-col>
					<el-col :span="11" :offset="1">
						<headerpageright style="background-color: #afd7ff;"></headerpageright>
					</el-col>
				</el-row>
			</el-container>
			<el-header style="background-color: #87c3ff;">
				<el-row :gutter="20">
					<el-col :span="17" :offset="3">
						<headernav style=""></headernav>
					</el-col>
				</el-row>
			</el-header>
		</el-container>
		<el-container style="padding:50px 0px 50px 0px">
			<el-row :gutter="20" style="width: 100%;">
				<el-col :span="14" :offset="3">
					<el-main v-bind:style="{minHeight: Height+'px'}" style="border: solid 1px gray;border-radius: 10px;">
						<component v-bind:is="privatecurrent"  style="padding: 5px 25px 25px 25px;"></component>
					</el-main>
				</el-col>
				<el-col :span="1">
					<el-aside style="height: 900px;">
						<el-main style="height:300px;">
							<el-header style="border: solid 1px gray;font-size: 30px;color: black;background-color: #afd7ff;border-radius:10px 10px 0px 0px;">公告</el-header>
							<el-main style="border: solid 1px gray;border-radius:0px 0px 10px 10px;height:200px;">
								<blognews></blognews>
							</el-main>
						</el-main>
						<el-main style="height:500px;">
							<el-header style="border: solid 1px gray;font-size: 30px;color: black;background-color: #afd7ff;border-radius:10px 10px 0px 0px;">分类</el-header>
							<el-main style="border: solid 1px gray;border-radius:0px 0px 10px 10px;">
								<blogitems></blogitems>
							</el-main>
						</el-main>
						<el-footer></el-footer>
					</el-aside>
				</el-col>
			</el-row>
		</el-container>
		<el-footer style="text-align: center;">Copyright © 2020 Anwesende Powered by Vue</el-footer>
	</div>
</template>

<script>
	import headernav from './components/headernav.vue'
	import mainpage from './components/Catalog/main.vue'
	import headerpageleft from './components/headerpage_left.vue'
	import rightaside from './components/Aside.vue'
	import blognews from './components/blognews.vue'
	import blogitems from './components/blogitems.vue'
	import headerpageright from './components/headerpage_right.vue'
	import cv from './components/Catalog/curriculum_vitae.vue'
	import blogmain from './components/Catalog/blogmain.vue'
	import github from './components/blog/github.vue'
	import csharpstring from './components/blog/csharpstring.vue'
	export default {
		name: 'app',
		components: {
			headernav,
			mainpage,
			rightaside,
			blognews,
			blogitems,
			headerpageright,
			headerpageleft,
			cv,
			blogmain,
			github,
			csharpstring
		},
		data() {
			return {
				Height: 0,
				Width: 0,
				privatecurrent: "mainpage"
			}
		},
		mounted() {
			//动态设置内容高度 让footer始终居底   header+footer的高度是100
			this.Height = document.documentElement.clientHeight - 100;
			//监听浏览器窗口变化　
			window.onresize = () => {
				this.Height = document.documentElement.clientHeight - 100
			}
		},

		computed: {
			changeCurrent() {
				return this.$store.state.current;
			}
		},
		watch: {
			changeCurrent() {
				this.privatecurrent = this.$store.state.current;
			}
		}
	}
</script>

<style>
	#app {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
	}
</style>
